import React, { Component } from 'react';
import { Button, Modal, Form, Input, notification } from 'antd';
import { MailOutlined } from '@ant-design/icons'
import { withRouter } from 'react-router-dom';
import { connect } from "react-redux";
import { changeCurrent } from '../../redux/actions/header';
import axios from 'axios';
import spinLogo from '../../assets/images/logo.svg'
import introStyles from './index.module.less'

const { TextArea } = Input;

class Introduce extends Component {

    state = {
        loading: false,
        visible: false,
        mail: "",
        suggestion: ""
    };

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleCancel = () => {
        this.setState({ visible: false });
    };

    handleMail = e => {
        console.log(e.target.value);
        this.setState({ mail: e.target.value });
    };

    handleSuggestion = e => {
        console.log(e.target.value);
        this.setState({ suggestion: e.target.value });
    };

    handleSubmit = values => {
        console.log(values);
        var mail = values['mail'].trim(), suggestion = values['suggestion'].trim();
        this.setState({ loading: true });
        setTimeout(() => { 
            axios.post('http://localhost:3000/api/feedback', { mail, suggestion}).then(
                response => {
                    if (response.status === 200 && response.data.code === 1) {
                        this.setState({suggestion: '', mail: '', loading: false, visible: false});
                        notification['success']({
                            duration: 3,
                            message: response.data.msg,
                            description:
                              ' 1941816504@qq.com ',
                        });
                    }
                    else {
                        notification['error']({
                            duration: 3,
                            message: '',
                            description: `${response.data.msg}`,
                        });
                    }
                },
                error => {
                    notification['error']({
                        duration: 3,
                        message: '',
                        description: `${error}`,
                    });
                }
            )
        }, 3000);
    }

    render() {
        const { visible, loading, suggestion, mail } = this.state;
        return (
            <div className={introStyles.introduce}>
                <div className={introStyles.introduce_text}>
                    <br />
                    
                    <p className={introStyles.detail}>
                    <br/>
                    <br/>
                    <br/>
                    <br/>
                    <br/>
                    <br/>
                    <br/>
                    tensorflow<br/>
                    
                    </p>
                </div>
                <div className={introStyles.introduce_btn}>
                    <Button className={introStyles.btn1} onClick={() => { this.props.changeCurrent({current: 'show'});this.props.history.push("/show/functionIntro") }}></Button>&nbsp;&nbsp;&nbsp;&nbsp;
                    <Button className={introStyles.btn2} onClick={this.showModal}></Button>
                    <Modal
                        className={introStyles.modal}
                        visible={visible}
                        title=""
                        onCancel={this.handleCancel}
                        footer={null}
                    >
                        <Form
                            name="feedback"
                            onFinish={this.handleSubmit}
                        >
                            <Form.Item
                                name="mail"
                                rules={[{ required: true, message: '', pattern: /^([a-zA-Z]|[0-9])(\w|)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/ }]}
                            >
                                <Input
                                    onChange={this.handleMail}
                                    value={mail}
                                    prefix={<MailOutlined />}
                                    placeholder="QQ"
                                />
                            </Form.Item>
                            <Form.Item
                                name="suggestion"
                                rules={[{ required: true, message: '' }]}
                            >
                                <TextArea
                                    onChange={this.handleSuggestion}
                                    placeholder=""
                                    rows={10}
                                    value={suggestion}
                                />
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="submit" loading={loading}>
                                    
                                </Button>&nbsp;&nbsp;&nbsp;
                                <Button key="back" onClick={this.handleCancel}>
                                    
                                </Button>
                            </Form.Item>
                        </Form>
                    </Modal>
                </div>
                <img className={introStyles.introduce_logo} src={spinLogo} width="15rem" height="15rem" alt="" />
            </div>
        );
    }
}

export default connect(
    state => ({ current: state.current }),
    { changeCurrent }
)(withRouter(Introduce));